import * as React from 'react';
import { Input,Icon,Button } from 'antd';
import {Link} from 'react-router-dom'
import axios from 'src/config/axios'
import './SignUp.scss'


interface ISignUpState {
    account:string,
    password:string,
    passwordConfirmation:string
}

class SignUp extends React.Component<any,ISignUpState>{
    constructor(props){
        super(props)
        this.state = {
            account:'',
            password:'',
            passwordConfirmation:''
        }
    }

    onChangeAccount = (e) => {
        this.setState(
            {
                account:e.target.value
            }
        )
    }

    onChangePassword = (e) => {
        this.setState(
            {
                password:e.target.value
            }
        )
    }
    onChangePasswordConfirmation = (e) => {
        this.setState(
            {
                passwordConfirmation:e.target.value
            }
        )
    }
    submit = async () => {
        const { account,password,passwordConfirmation } = this.state;
        try{
            await axios.post('sign_up/user',{
                account,
                password,
                password_confirmation:passwordConfirmation
            })
        }catch(e){
            throw new Error(e)
        }
    }
    public render(){
        const { account,password,passwordConfirmation } = this.state;
        return(
            <div className="SignUp">
                <p className="title"></p>
                <Input
                    placeholder=""
                    prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    value={ account}
                    onChange={this.onChangeAccount}
                />
                <Input.Password value={password} placeholder="" onChange={this.onChangePassword} />
                <Input.Password value={passwordConfirmation} placeholder="" onChange = {this.onChangePasswordConfirmation} />
                <Button onClick={ this.submit}></Button>
                 <p> <Link to='/login'></Link></p>
            </div>
        )
    }
}

export default SignUp



